.card_row {
  position: relative;
  padding-right: 30px;
}
.card_row:not(:last-child)::before {
  position: absolute;
  content: '';
  left: calc(100%/6/2 - 30px/2 - 14px/2);
  bottom: 10px;
  width: 14px;
  height: 14px;
  box-sizing: border-box;
  border: 2px solid #dcdcde;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
}
.card_row:not(:last-child)::after {
  position: absolute;
  content: '';
  left: calc(100%/6);
  bottom: 10px;
  width: calc(100%/6*5 - 30px);
  border: 1px dashed #dcdcde;
}
.card_list {
  display: flex;
  flex-direction: column;
  padding: 20px 30px;
}
.card_list .card_row {
  display: flex;
  padding: 5px 0;
}
.card_list .card_row .card_main {
  width: calc(100%/6 - 30px);
  flex-shrink: 0;
  margin-right: 30px;
}
.card_list .card_row .card_items {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
}
.card_list .card_row .card_items .card_item,
.card_list .card_row .card_items .card_btn,
.card_list .card_row .card_items .card_add {
  width: calc(100%/(6 - 1) - 30px);
  margin-right: 30px;
  margin-bottom: 30px;
}
.card_list .card_row .card_items .card_item.dragstart,
.card_list .card_row .card_items .card_btn.dragstart,
.card_list .card_row .card_items .card_add.dragstart {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_list .card_row .card_items .card_item.dragstart {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_list .card_row .card_items .card_item.dragover {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_list .card_row.dragstart {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_list .card_row.dragover {
  background-color: rgba(0, 0, 0, 0.3);
}
.card_main,
.card_item,
.card_btn,
.card_add {
  position: relative;
  background-color: #fff;
  box-sizing: border-box;
  height: 80px;
  user-select: none;
}
.card_item {
  display: flex;
  align-items: center;
}
.card_item .card_icon_ct {
  flex-shrink: 0;
  padding: 10px;
  width: 60px;
  box-sizing: border-box;
  height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
}
.card_item .card_icon_ct .card_icon {
  width: 100%;
  height: 100%;
}
.card_item .card_content {
  display: flex;
  flex-direction: column;
  width: calc(100% - 60px);
}
.card_item .card_content .card_en_name,
.card_item .card_content .card_cn_name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card_item .card_content .card_en_name {
  color: #595959;
}
.card_item .card_content .card_cn_name {
  color: #8f8f8f;
}
.card_item .icon_remove,
.card_item .icon_edit {
  position: absolute;
  right: 5px;
  cursor: pointer;
}
.card_item .icon_remove {
  top: 5px;
}
.card_item .icon_edit {
  bottom: 5px;
}
.card_main {
  display: flex;
  align-items: center;
  padding-left: 20px;
}
.card_main .card_content {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.card_main .card_content .card_en_name,
.card_main .card_content .card_cn_name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card_main .card_content .card_en_name {
  font-weight: bold;
  color: #2c3892;
}
.card_main .card_content .card_cn_name {
  color: #8f8f8f;
}
.card_main .icon_edit {
  position: absolute;
  right: 5px;
  bottom: 5px;
  cursor: pointer;
}
.card_add {
  outline: 2px dashed #2c3892;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #2c3892;
  cursor: pointer;
}
.card_add .icon {
  width: 30px;
  height: 30px;
  margin-bottom: 5px;
}
.card_btn {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page_ct {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.page_ct > .page_top {
  width: 100%;
  height: 50px;
  background: #2f3ba2;
}
.page_ct > .page_content {
  display: flex;
  height: calc(100% - 50px);
}
.page_left {
  height: 100%;
  width: 250px;
  flex-shrink: 0;
  transition: all .3s;
  background-color: #fff;
  box-shadow: 0 0 5px #999;
}
.page_left.collapse {
  margin-left: -250px;
  position: relative;
  z-index: 2;
}
.page_left.collapse .collapse_left {
  transform: translateX(60px);
  background-color: rgba(255, 255, 255, 0.5);
  padding: 5px;
}
.page_left.collapse + .page_right .right_head {
  padding-left: 60px;
}
.page_right {
  width: calc(100% - 250px);
  flex-grow: 1;
}
.left_head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  padding-left: 20px;
  border-bottom: 1px solid #ddd;
  box-sizing: border-box;
  color: #2f3ba2;
  font-weight: bold;
  font-size: 18px;
}
.left_head .oper_list {
  display: flex;
  align-items: center;
  padding-right: 20px;
}
.left_head .oper_list li {
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.left_head .oper_list .collapse_left {
  transition: all .3s;
}
.left_list {
  height: calc(100% - 70px);
  user-select: none;
}
.left_list_item {
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding-left: 25px;
}
.left_list_item .icon {
  flex-shrink: 0;
}
.left_list_item .menu_name {
  padding-left: 15px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.right_head {
  display: flex;
  align-items: center;
  height: 70px;
  border-bottom: 1px solid #ddd;
  box-sizing: border-box;
  color: #2f3ba2;
  font-weight: bold;
  font-size: 16px;
  padding-left: 20px;
  transition: all .3s;
}
.right_head .system_name_en {
  margin-right: 10px;
}
.page_set {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.page_set .page_set_content {
  position: absolute;
  background-color: #fff;
  top: 50%;
  height: 600px;
  margin-top: -300px;
  left: 50%;
  margin-left: -375px;
  width: 750px;
  display: flex;
  padding: 10px 50px;
  justify-content: space-between;
  padding-bottom: 100px;
  box-sizing: border-box;
  box-shadow: 0 0 5px #333;
}
.page_set .page_set_content .set_left {
  width: 200px;
  border: 1px solid #ccc;
}
.page_set .page_set_content .set_left .set_left_head {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  border-bottom: 1px solid #ccc;
  font-weight: bold;
  box-sizing: border-box;
}
.page_set .page_set_content .set_left .left_list {
  height: 460px;
  overflow: auto;
}
.page_set .page_set_content .set_left .left_list .left_list_item {
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 25px;
}
.page_set .page_set_content .set_left .left_list .left_list_item .icon {
  flex-shrink: 0;
}
.page_set .page_set_content .set_left .left_list .left_list_item .menu_name {
  padding-left: 15px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.page_set .page_set_content .set_left .left_list .left_list_item.dragstart {
  background-color: #ccc;
}
.page_set .page_set_content .set_left .left_list .left_list_item.dragempty {
  color: transparent;
  background-color: #eee;
}
.page_set .page_set_content .set_right {
  width: 350px;
  border: 1px solid #ccc;
}
.page_set .page_set_content .set_right .set_right_head {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  border-bottom: 1px solid #ccc;
  font-weight: bold;
  box-sizing: border-box;
}
.page_set .page_set_content .set_right .right_list {
  height: 460px;
  overflow: auto;
}
.page_set .page_set_content .set_right .right_list .system_item {
  display: flex;
  flex-direction: column;
}
.page_set .page_set_content .set_right .right_list .system_item .system_name {
  padding: 5px;
  background-color: #ccc;
  cursor: pointer;
}
.page_set .page_set_content .set_right .right_list .system_item:not(:first-child) {
  border-top: 1px solid #ddd;
}
.page_set .page_set_content .set_right .right_list .system_item:not(.active) .system_name {
  background-color: #eee;
}
.page_set .page_set_content .set_right .right_list .system_item:not(.active) .system_name:hover {
  background-color: #ccc;
}
.page_set .page_set_content .set_right .right_list .system_item:not(.active) .menus {
  display: none;
}
.page_set .page_set_content .set_right .right_list .menus .menu_item {
  display: flex;
  align-items: center;
  padding: 5px;
  cursor: pointer;
}
.page_set .page_set_content .set_right .right_list .menus .menu_item .menu_check_flag {
  position: relative;
  display: flex;
  width: 16px;
  height: 16px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  margin-right: 10px;
  align-items: center;
  justify-content: center;
}
.page_set .page_set_content .set_right .right_list .menus .menu_item.checked .menu_check_flag:before {
  content: '';
  width: 8px;
  height: 8px;
  background-color: #333;
}
.page_set .page_set_content .set_oper {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page_set .page_set_content .set_oper .btn {
  margin: 0 30px;
}
